<template>

    <div
        v-if="currentOrder"
        class="credit-order-wrapper">

        <div class="section-title section-title-row">

            <div class="section-title-left">
                <h1>Кредитная программа «{{currentOrder.name}}»</h1>
                <div
                    class="credit-status"
                    :class="orderStatus(currentOrder.status).status"
                    v-html="orderStatus(currentOrder.status).title" />
            </div>

            <div class="section-title-right">
                <a v-if="questionnaire.length > 0"
                   href="#"
                   class="download-link-2"
                   @click="downloadFile()"><span class="icon-download-3"></span>Скачать сформированную заявку</a>
            </div>

        </div>

        <div class="block">
            <div class="block-title">
                <div class="h2">Информация о кредите</div>
            </div>
            <div class="block-content">
                <div class="loans-issued-item no-hover">
                    <div class="load-issued-img">
                        <img :src="require('@/assets/images/dist/credit-img--1.png')" alt="">
                    </div>
                    <div class="loans-issued-title">
                        <div class="h4">Кредит «{{currentOrder.name}}»</div>
                        <p>{{currentTarget}}</p>
                    </div>
                    <div class="loans-price">Сумма <span>{{currentOrder.sum | numberFilter}}</span></div>
                    <div class="loans-conditions">
                        <ul>
                            <li>Ставка: <span>{{ currentOrder.insurance ? currentTariff.rateWithInsurance : currentTariff.rateWithoutInsurance }}%</span></li>
                            <li>Комиссия: <span>{{ getCommission() }} руб.</span></li>
                            <li>Страхование жизни: <span>{{ currentOrder.insurance ? 'Да' : 'Нет' }}</span></li>
                            <li>Срок: <span>{{currentOrder.term}} {{$options.filters.declensionNumbers(currentOrder.term, ['месяц', 'месяца', 'месяцев'])}}</span></li>
                            <li>Дата создания заявки: <span>{{parseDate(currentOrder.created_at)}}</span></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>

        <div class="block">
            <div class="block-title">
                <div class="h2">Документы</div>
            </div>
            <div class="block-content">
                <div class="documents-list documents-list-with-links">
                    <ul>
                        <li v-for="doc in currentOrderDocs"
                            :key="doc.id">

                            <div class="document-item">
                                <div class="document-item-title">
                                    {{doc.name}}
                                </div>
                                <div class="document-files-list">
                                    <div v-for="file in doc.files"
                                         :key="file.id"
                                         class="files-item">
                                        <span class="document-link"
                                              @click="openDocViewModal(file)">
                                            <span class="icon-document"></span>
                                            <span class="document-link-main">
                                                <span class="document-link-title">{{file.name}}.{{file.format}}</span>
                                                <span class="document-link-size">({{convertSize(file.size)}})</span>
                                            </span>
                                        </span>
                                    </div>
                                </div>
                            </div>

                            <div class="document-download-link">
                                <a
                                    href="#"
                                    class="download-link"
                                    @click.prevent="downloadFile(doc)">Скачать</a>
                            </div>

                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <DocView v-if="viewDocumentModal"
                 :list="viewDocument"
                 @close-modal="viewDocumentModal = false"
                 :familiarizedBtn="false"/>

        <Popup v-if="docViewErrorModal"
               :error="true"
               @close-modal="docViewErrorModal = false">

            <div class="popup-title">
                <div class="h3">Документы формата DOC, DOCX, XLS и XLSX нельзя просматривать через браузер</div>
            </div>

            <div class="popup-body">
                <p>Для просмотра данного документа Вам необходимо его скачать</p>
                <button
                    class="btn btn-lg btn-block close-modal"
                    @click.prevent="downloadFile(downloadedDoc)">Скачать</button>
            </div>

        </Popup>

    </div>

</template>

<script>

    import DocView from "@/components/Blocks/DocView"
    import Popup from "@/components/Blocks/Popup"
    import {mapState, mapGetters} from 'vuex'
    import moment from "moment";

    export default {
        components: {
            DocView,
            Popup
        },
        computed: {
            ...mapState({
                currentOrder: state => state.createOrder.orderFullInfo.order,
                questionnaire: state => state.createOrder.neededToLoadFiles,
                currentOrderDocs: state => state.orders.orderDocs,
                tariffs: state => state.tariffs.tariffs
            }),
            ...mapGetters({
                targets: 'getTarrifsTargets'
            }),
            currentTariff () {
                return this.tariffs.filter(item => item.id === this.currentOrder.tariff_id)[0]
            },
            currentTarget() {
                const target = this.targets.filter(item => item.id === parseInt(this.currentOrder.purpose))[0]
                return target ? target.title : ''
            }
        },
        data: () => ({
            viewDocumentModal: false,
            docViewErrorModal: false,
            downloadedDoc: '',
            viewDocument: []
        }),
        methods: {
            orderStatus(id) {
                switch (id) {
                    case 1:
                        return {
                            title: 'Новая заявка',
                            status: 'secondary'
                        }
                    case 2:
                        return {
                            title: 'Отправлено в банк',
                            status: 'secondary'
                        }
                    case 3:
                        return {
                            title: 'Ошибка отправки в Банк<br>(Свяжитесь с банком: 8 800 775-18-77)',
                            status: 'danger'
                        }
                    case 4:
                        return {
                            title: 'Отправлено в банк',
                            status: 'secondary'
                        }
                    case 5:
                        return {
                            title: 'Ошибка отправки в Банк<br>(Свяжитесь с банком: 8 800 775-18-77)',
                            status: 'danger'
                        }
                    case 6:
                        return {
                            title: 'Рассматривается банком',
                            status: 'secondary'
                        }
                    case 7:
                        return {
                            title: 'Отклонен',
                            status: 'danger'
                        }
                    case 8:
                        return {
                            title: 'Дополнительные документы',
                            status: 'secondary'
                        }
                    case 9:
                        return {
                            title: 'Одобрен',
                            status: 'secondary'
                        }
                    case 10:
                        return {
                            title: 'Отклонен пользователем',
                            status: 'danger'
                        }
                    case 11:
                        return {
                            title: 'Рассматривается банком',
                            status: 'warning'
                        }
                    case 12:
                        return {
                            title: 'Рассматривается банком',
                            status: 'secondary'
                        }
                    case 13:
                        return {
                            title: 'Подготовка документов',
                            status: 'warning'
                        }
                    case 14:
                        return {
                            title: 'Подписание',
                            status: 'success'
                        }
                    case 15:
                        return {
                            title: 'Идет обработка данных',
                            status: 'secondary'
                        }
                    case 16:
                        return {
                            title: 'Ошибка отправки в Банк<br>(Свяжитесь с банком: 8 800 775-18-77)',
                            status: 'danger'
                        }
                    case 17:
                        return {
                            title: 'Отправлено в банк',
                            status: 'success'
                        }
                    case 18:
                        return {
                            title: 'Ошибка отправки в Банк<br>(Свяжитесь с банком: 8 800 775-18-77)',
                            status: 'danger'
                        }
                    case 19:
                        return {
                            title: 'Проверка документов',
                            status: 'warning'
                        }
                    case 20:
                        return {
                            title: 'Кредит выдан',
                            status: 'success'
                        }
                    default:
                        return {
                            title: '',
                            status: ''
                        }
                }
            },
            convertSize(size) {
                if ( size > 999 && size < 1000000) {
                    return `${(size / 1000).toFixed(2)}КБ`
                }
                else if ( size > 999999 && 1000000000 ) {
                    return `${(size / 1000000).toFixed(2)}МБ`
                }
                else {
                    return `${size.toFixed(2)}Б`
                }
            },
            openDocViewModal(doc) {
                if (doc.format === 'pdf' || doc.format === 'jpg' || doc.format === 'png') {
                    this.viewDocument = doc
                    this.viewDocumentModal = true
                } else {
                    this.downloadedDoc = doc
                    this.docViewErrorModal = true
                }
            },
            closeViewDocModal() {
                this.checkDocsViews = []
                this.checkDocs = false
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            },
            downloadFile(doc) {
                if (doc.files) {
                    doc.files.forEach(file => {
                        this.$store.dispatch('getFile', file.id).then(content => {
                            this.$store.dispatch('downloadFile', {content, name: file.name, format: file.format})
                        })
                    })
                } else {
                    this.$store.dispatch('getFile', doc.id).then(blob => {
                        this.$store.dispatch('downloadFile', {content: blob, name: doc.name, format: doc.format})
                    })
                }
            },
            getCommission () {
                const res = this.currentOrder.sum * this.currentTariff.commissionPercent / 100
                return res > this.currentTariff.minCommission ? res : this.currentTariff.minCommission
            }
        },
        async mounted() {
            let id = this.$route.params.id
            if (this.tariffs.length === 0) {
                await this.$store.dispatch('getTariffs')
            }
            await this.$store.dispatch('getCurrentOrder', id)
            await this.$store.dispatch('getDetailOrderDocs', id)
        }
    }
</script>
